
#cloud {
    height: 230px;
    margin: 40px;
    position: absolute;
    width: 300px;
    z-index: 1;
    animation: nub infinite 8s ease;
}
#cloud div {
   box-shadow: 0px 0px 2px 2px 5px white;
}

#bottom_c {
    background-color: #fff;
   box-shadow: 2px 2px 5px white;

    border-radius: 100px;
    height: 150px;
    position: absolute; 
    top: 100px;
    width: 350px;
    z-index: 1;
}

#right_c{
    background-color: #fff;
   box-shadow: 2px 2px 5px white;

    border-radius: 100%;
    height: 150px;
    left: 140px;
    position: absolute; 
    top: 40px; 
    width: 150px;
    z-index: -1;
}

#left_c{
    background-color: #fff; 
   box-shadow: 2px 2px 5px white;
    border-radius: 100%;
    height: 100px;
    left: 50px;
    position: absolute; 
    top: 70px; 
    width: 100px;
    z-index: -1;
}

#cloud::before {
   box-shadow: 2px 2px 5px white;

    background-color: white;
    border-radius: 50%;
    content: '';
    height: 100px;
    left: 55px;
    position: absolute; 
    top: 75px; 
    width: 100px;
    z-index: 1;
}

#cloud::after {
   box-shadow: 2px 2px 5px white;

    position: absolute; top: 45px; left: 145px;
    background-color: white;
    border-radius: 50%;
    content: '';
    width: 150px;
    height: 150px;
    z-index: 1;
}

@media screen and (max-width:800px){
    #cloud{
        display:none;
    }
}
@keyframes nub{
    0%
    {
        transform: translateX(5px,5px) scale(.5);
        
    }
    20%
    {
        transform: translateX(10px,10px)scale(.5); 
    }
    40%
    {
        transform: translate(15px,15px)scale(.5);
      
    }

}