.m{
    transition: all 1s ease-in;
}
.m:hover{
    transform:rotate(180deg);
    
}
.columntext{
    text-align:justify;
    padding-left:20vh;
    padding-right:20vh;

    background:linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%)!important;
    margin-left:-1.5em;
    margin-right:-1.5em;
    color:white;
    padding-top:5vh;
    padding-bottom:5vh;
}
.br{
    display:none;
}
@media screen and (max-width: 360px){
    .columntext{
        padding-left:2vh;
        padding-right:2vh;
    }
    .br{
        display:block;
    }
}
.til{
    font-size: 2em;
    
}
@media screen and (max-width: 360px){
    .columntext{
        padding-left:2vh;
        padding-right:2vh;
    }
}
@media screen and (max-width: 360px){
    .til{
       font-size: 1em;
    }
    .ty{
        height: 15vh!important;
    }
}
@media screen and (max-width:430px){
    .log{
        width: 90%!important;
    }
}
.hr{
    animation-name: hr ;
    animation-duration: 1s;
    animation-iteration-count: 5;
   
}
.hr1{
    animation-name: hr ;
    animation-duration: 1.5s;
    animation-iteration-count: 5;
    
}
.hr2{
    animation-name: hr ;
    animation-duration: 2s;
    animation-iteration-count: 5;
    
}
@keyframes hr {
    0%{
        width: 100%;
        border-color:#ff2e93;
        background-color: #ff2e93;
    }
    100%{
        width: 0%;
    }
}
.fadekey{
    animation: fade normal 3s ease-in;
}
._image{
    animation: image normal 3s ease-in;
}
@keyframes fade{
    0%
    {
        transform: translate(-600px);
        opacity: 0;
    }
    10%
    {
        transform: translate(-500px);
        opacity: 0;
    }
    20%
    {
        transform: translate(-400px);
        opacity: .2;
        color:#ff2e93;
    }
    40%
    {
        transform: translate(-300px);
        opacity: .4;
        color:#ff2e93;
    }
    60%
    {
        transform: translate(-200px);
        opacity: .6;
        color:#ff2e93;
    }
    80%
    {
        transform: translate(-100px);
        opacity: .8;
    }
    100%
    {
        transform: translate(0px);
        opacity: 1;
    }
}
@keyframes image{
    0%
    {
        transform: translate(0,-600px);
        opacity: 0;
    }
    10%
    {
        transform: translate(0,-500px);
        opacity: 0;
    }
    20%
    {
        transform: translate(0,-400px);
        opacity: .2;
        color:#ff2e93;
    }
    40%
    {
        transform: translate(0,-300px);
        opacity: .4;
        color:#ff2e93;
    }
    60%
    {
        transform: translate(0,-200px);
        opacity: .6;
        color:#ff2e93;
    }
    80%
    {
        transform: translate(-100px);
        opacity: .8;
    }
    100%
    {
        transform: translate(0px);
        opacity: 1;
    }
}